import { _decorator, Component, Node, input, Input, Event, EventKeyboard, macro, Vec2, RigidBody2D, Collider2D, BoxCollider2D, Contact2DType, IPhysics2DContact, Label, KeyCode, EventMouse} from 'cc';
const { ccclass, property } = _decorator;

@ccclass('TouchDragger')
export class TouchDragger extends Component {

    onLoad() {
      
           this.node.on(Node.EventType.TOUCH_START, (event) => { // При нажатии кнопки мыши в области целевого узла
            console.log('Touch start');
            event.propagationStopped = true;
            this.node.setSiblingIndex(36);
          }, this);

          this.node.on(Node.EventType.TOUCH_MOVE, (event) => { // При нажатии кнопки мыши в области целевого узла
            console.log('Touch move');
           
            this.node.setPosition(this.node.getPosition().x + event.touch.getDelta().x , this.node.getPosition().y + event.touch.getDelta().y, 0);
           // console.log('x ='+ this.node.getPosition().x);
           // console.log('y ='+ this.node.getPosition().y);
           
          // event.propagationStopped = true
       
          }, this);

          this.node.on(Node.EventType.TOUCH_END, (event) => { // При нажатии кнопки мыши в области целевого узла
            console.log('Touch end');
          }, this);

    }

}